.ChScroll-Container {
	background-color: var(--background);
	width: 330px;
	height: 65px;
	border-radius: 30px;
	position: relative;
	margin-top: 15px;
	padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
}
.ChScroll-Container::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	filter: blur(3px);
	background-color: var(--blue-munsell);
	width: 100%;
	height: 70px;
	border-radius: 30px;
}
.ChScroll-ul {
	margin-inline-start: 0;
	margin-block-end: 0;
	margin-block-start: 0;
	margin-inline-end: 0;
	list-style: none;
	display: flex;
	flex-direction: row;
	align-content: center;
	padding: 0;
	overflow-y: hidden;
	overflow-x: auto;
	border-radius: 30px;
}
.ChScroll-ul::-webkit-scrollbar {
	height: 8px;
}
.ChScroll-ul::-webkit-scrollbar-track {
	background: transparent;
}
.ChScroll-ul::-webkit-scrollbar-thumb {
	background-color: transparent;
	border-radius: 10px;
	border: 2px solid var(--blue-munsell);    
}
.ChScroll-li {
	cursor: pointer;
	margin-left: 8px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
}
.ChScroll-ChImg-Alive {
	background-color: white;
	width: 55px;
	height: 55px;
	border-radius: 50%;
	z-index: 2;
	position: relative;
}
